export default {
	props: {
		value: {},
		range: {},
		rangeKey: {},
		placeholder: {}
	},
	created () {
		this.initIndex()
	},
	data () {
		return {
			index: 0
		}
	},
	methods: {
		// 初始化索引
		initIndex () {
			if (this.value) {
				if (this.rangeKey) {
					this.index = this.range.find((v, i) => {
						if (this.value === v.id) {
							return i
						}
					})
				} else {
					this.index = this.range.find((v, i) => {
						if (this.value === v) {
							return i
						}
					})
				}
			} else {
				this.index = 0
			}
		},
		change (e) {
			this.$emit('change', e)
			this.$emit('input', this.rangeKey ? this.range[this.index].id : this.range[this.index])
		}
	}
}
